.about {
  position: relative;
  font-family: "Space Grotesk", serif;

  @include pageGrid;

  &__title {
    grid-column: 1/-1;

    @include onTablet {
      grid-column: 1/4;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__content {
    grid-column: 1/-1;
    font-family: "Space Mono", serif;

    @include onTablet {
      grid-column: 4/7;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__description {
    margin: 0;
    line-height: 140%;
  }
}
